@font-face {
  font-family: "Inter";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/Inter-Medium.woff2");
}
@font-face {
  font-family: "Inter Bold";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/Inter-Bold.woff2");
}
@font-face {
  font-family: "Inter Black";
  font-weight: 900;
  font-style: normal;
  src: url("../fonts/Inter-Black.woff2");
}
:root {
  --primary-color: #D8FF00;
  --secondary-color: #5E6F00;
  --tertiary-color: #B6D600;
  --white-color: #FFF;
  --black-color: #333333;
  --font-normal: "Inter", sans-serif;
  --font-bold: "Inter Bold", sans-serif;
  --font-black: "Inter Black", sans-serif;
  --leading-loose: 34px;
  --container: 1152px;
}

.wp-block-buttons > .wp-block-button.is-style-small-button {
  position: relative;
  display: flex;
  align-items: center;
}
.wp-block-buttons > .wp-block-button.is-style-small-button::after {
  background-color: var(--secondary-color);
  content: "";
  display: block;
  height: 18px;
  width: 18px;
  mask-image: url(data:image/svg+xml;base64,);
  mask-repeat: no-repeat;
  mask-size: 100%;
  margin-top: 3px;
  transition: all 0.3s ease-out;
  transform: translateX(0);
  position: initial;
}
.wp-block-buttons > .wp-block-button.is-style-small-button .wp-block-button__link {
  background: transparent;
  color: var(--secondary-color);
  padding-left: 0;
  padding-right: 16px;
}
.wp-block-buttons > .wp-block-button.is-style-small-button:hover::after {
  transform: translateX(-10px);
}

.wp-site-blocks {
  max-width: var(--container);
  margin: 0 auto;
}

h1 strong,
h2 strong,
h3 strong,
h4 strong {
  color: var(--tertiary-color);
}

p {
  line-height: var(--leading-loose);
}
p strong {
  font-family: var(--font-bold);
}

.alignwide,
.alignfull {
  margin: 0 calc(50% - 50vw);
  max-width: 100vw;
  width: 100vw;
}

.pattern-hero,
.pattern-headline-with-cards {
  margin: 100px 0;
}

.float-left-width-30 {
  float: left;
  max-width: 33.3333%;
  padding: 32px;
}

.float-left-width-50 {
  float: left;
  max-width: 50%;
  padding: 32px;
}

.float-right-width-30 {
  float: right;
  max-width: 33.3333%;
  padding: 32px;
}

.float-right-width-50 {
  float: right;
  max-width: 50%;
  padding: 32px;
}

.float-none {
  float: none;
}

.single .wp-block-template-part {
  position: relative;
}
.single .wp-block-template-part .header {
  position: absolute;
  width: 100%;
  z-index: 10;
  filter: brightness(200%);
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container {
  max-width: var(--container);
  margin: auto;
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container .wp-block-group {
  display: inline-flex;
  align-items: center;
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container .wp-block-group .wp-block-post-author__avatar .avatar {
  border-radius: 9999px;
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container .wp-block-group .wp-block-post-author__content {
  display: flex;
  align-items: center;
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container .wp-block-group .wp-block-post-author__content .wp-block-post-author__name {
  color: var(--primary-color);
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container .wp-block-group .wp-block-post-date time {
  color: var(--primary-color);
}
.single .wp-block-cover.post-hero .wp-block-post-terms a {
  color: var(--primary-color);
  text-decoration: none;
}
.single .wp-block-cover.post-hero .wp-block-post-title {
  color: var(--primary-color);
  margin-top: 0;
  margin-bottom: 0;
}

.pattern-cards .wp-block-post-template .wp-block-post .pattern-group {
  display: flex;
  min-height: 248px;
  border-radius: 4px;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group > .wp-block-group {
  align-content: space-between;
  display: flex;
  position: relative;
  width: 100%;
  margin-top: 0;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group > .wp-block-group:first-child {
  padding: 16px;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .wp-block-post-title {
  padding: 0;
  width: 100%;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .wp-block-post-title a {
  color: var(--primary-color);
  font-family: var(--font-normal);
  font-size: 1.17em;
  text-decoration: none;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .wp-block-post-terms {
  position: absolute;
  z-index: 10;
  right: 8px;
  top: 16px;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .wp-block-post-terms a {
  background-color: rgba(29, 29, 29, 0.8);
  padding: 4px 8px;
  font-family: var(--font-normal);
  font-size: 16px;
  border-bottom-left-radius: 8px;
  border-top-right-radius: 8px;
  text-decoration: none;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .wp-block-post-featured-image,
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .pattern-card-author {
  align-items: center;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .pattern-card-author .wp-block-post-author__avatar .avatar {
  border-radius: 9999px;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .pattern-card-author .wp-block-post-author__name {
  color: var(--primary-color);
}

.wp-block-columns.pattern-headline-with-cards .pattern-headline-col:nth-child(2) {
  display: flex;
  align-items: center;
}
.wp-block-columns.pattern-headline-with-cards .pattern-headline-content .has-section-header-font-size {
  font-family: var(--font-bold);
}